.paperAddNews {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    width: 720px;
    height: 750px;
    background-color: white;
    padding: 20px;
    .footer {
        margin-top: 2px;
        text-align: right;
        .buttonSave {
            margin: 5px;
            padding: 10px;
            width: 100px;
            border: 1px solid #c4c4c4;
            color: #c4c4c4;
            background-color: #fff;
            border-radius: 5px;
            &:hover {
                margin: 5px;
                padding: 10px;
                width: 100px;
                border: 1px solid #c4c4c4;
                color: #fff;
                background-color: #c4c4c4;
                border-radius: 5px;
            }
        }
        .buttonClose {
            margin: 5px;
            padding: 10px;
            width: 100px;
            border: 1px solid #ED4646;
            color: #ED4646;
            background-color: #fff;
            border-radius: 5px;
            &:hover {
                margin: 5px;
                padding: 10px;
                width: 100px;
                border: 1px solid #ED4646;
                color: #fff;
                background-color: #ED4646;
                border-radius: 5px;
            }
        }
    }
    .form {
        display: flex;
        flex-direction: column;
        // padding-bottom: 20px;
        border-radius: 5px;
        .InfoCreate {
            width: 100%;
            background-color: #c4c4c4;
            height: 200px;
            display: flex;
            justify-content: center;
            align-items: center;
            .title {
                background-color: white;
                border: 0.5px solid grey;
                border-radius: 5px;
            }
        }
        .simpleModalItem {
            height: 100px;
        }
    }
}

.header {
    margin: 0 0 10px 0;
}

.title {
    margin-bottom: 10px;
    width: 100%;
}

.textarea {
    padding: 10px;
    margin-bottom: 10px;
}